import { Component, OnInit } from "@angular/core";
import { AuthService } from "../auth.service";
import { Buyer } from "./forms";
import { FormsService } from "./forms.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { FormControl, Validators } from "@angular/forms";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA
} from "@angular/material";
import { AlertDialogComponent } from "../alert-dialog/alert-dialog.component";
import { ConfirmationDialog } from "./confirmation-dialog.component";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { StateServiceService } from "../state-service.service";
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

import {
  AngularFirestore,
  AngularFirestoreDocument
} from "@angular/fire/firestore";
import { RouterLink } from "@angular/router";
import * as firebase from "firebase";
export interface State {
 
  name: string;
  
}
@Component({
  selector: "app-forms",
  templateUrl: "./forms.component.html",
  styleUrls: ["./forms.component.css"]
})

export class FormsComponent implements OnInit {
  isLoggedIn: Boolean = true;
  userData: any;
  Buyer: Buyer = new Buyer();
  submitted = false;
  DatafieldsService: any;
  financial: any[];
  links = ["First", "Second", "Third"];
  activeLink = this.links[0];
  background = "";
  selectedIndex = 0;
  maxNumberOfTabs = 2;
  isLoading: boolean = false;
  return: any;
  version = VERSION;
 

  // options: string[] = ['SW5101', 'NW7W25', 'W25R43'];
  postcodeFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(6)
  ]);
  filteredStates: Observable<State[]>;

  states: State[] = [
    {
      name: 'SW5101'

    },
    {
      name: 'NW7W25',

    },
    {
      name: 'W25R43',

    },
    
  ];

  toggleBackground() {
    this.background = this.background ? "" : "primary";
  }

  addLink() {
    this.links.push(`Link ${this.links.length + 1}`);
  }

  constructor(
    public authService: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    private formsService: FormsService,
    private firestore: AngularFirestore,
    public afAuth: AngularFireAuth,
    db: AngularFirestore,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private router: Router,
    private stateService: StateServiceService
  ) {
    this.filteredStates = this.postcodeFormControl.valueChanges
    .pipe(
      startWith(''),
      map(state => state ? this._filterStates(state) : this.states.slice())
    );
}
private _filterStates(value: string): State[] {
  const filterValue = value.toLowerCase();

  return this.states.filter(state => state.name.toLowerCase().indexOf(filterValue) === 0);
}
 
  ngOnInit() {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        this.LoggedOut();
      }
    });
  }

  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }

  newCustomer(): void {
    this.submitted = false;
    this.Buyer = new Buyer();
  }

  onSubmit() {
    this.stateService.Buyer = this.Buyer;
    this.router.navigate(["buyerdetails"]);
  }

  emailFormControl = new FormControl("", [
    Validators.required,
    Validators.email
  ]);

  lastnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  firstnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  AddressFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  TownFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

  stateFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(15)
  ]);

 
  addressnameFormControl = new FormControl("", [
    Validators.required,
    Validators.maxLength(6)
  ]);
}
