import { Component, OnInit } from "@angular/core";
import { AngularFireAuth } from "@angular/fire/auth";
import {
  AngularFirestore,
  AngularFirestoreDocument
} from "@angular/fire/firestore";
import { Seller } from "./seller";
import { SellerformService } from "./sellerform.service";
import * as firebase from "firebase";
import { AuthService } from "../auth.service";
import {FormControl, Validators} from '@angular/forms';
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA
} from "@angular/material";
import { from } from "rxjs";
import { StateServiceService } from "../state-service.service";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
export interface State {
 
  name: string;
  
}
@Component({
  selector: "app-sellerform",
  templateUrl: "./sellerform.component.html",
  styleUrls: ["./sellerform.component.css"]
})
export class SellerformComponent implements OnInit {
  isLoggedIn: Boolean = true;
  userData: any;
  Seller: Seller = new Seller();
  submitted = false;
  isLoading: boolean = false;
  return: any;
  version = VERSION;
  ConfirmDetailService: any;    
  postcodeFormControl = new FormControl('', [
      Validators.required,
      Validators.maxLength(6),
    ]);
  filteredStates: Observable<State[]>;

  states: State[] = [
    {
      name: 'SW5101'

    },
    {
      name: 'NW7W25',

    },
    {
      name: 'W25R43',

    },
    
  ];

  constructor(
    public authService: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth,
    private firestore: AngularFirestore,
    private SellerformService: SellerformService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private router: Router,
    private stateService: StateServiceService
  ) {
    this.filteredStates = this.postcodeFormControl.valueChanges
    .pipe(
      startWith(''),
      map(state => state ? this._filterStates(state) : this.states.slice())
    );


  }



  private _filterStates(value: string): State[] {
    const filterValue = value.toLowerCase();
  
    return this.states.filter(state => state.name.toLowerCase().indexOf(filterValue) === 0);
  }
  ngOnInit() {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem("user"));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        JSON.parse(localStorage.getItem("user"));
        this.LoggedOut();
      }
    });
  }

  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }
  newCustomer(): void {
    this.submitted = false;
    this.Seller = new Seller();
  }

  onSubmit() {
    // Get Seller variable into local scope for html
    this.stateService.Seller = this.Seller;
    this.router.navigate(["confirmation"]);
  }

  FirstnameFormControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(15),
  ]);
  LastnameFormControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(15),
  ]);
  EmailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);
  AddressFormControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(15),
  ]);
  TownFormControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(15),
  ]);
  stateFormControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(15),
  ]);

  addressnameFormControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(6),
  ]);
  noroomsFormControl = new FormControl('', [
    Validators.required,
    Validators.maxLength(2),
  ]);
  DOBFormControl = new FormControl('', [
    Validators.required,
  ]);

  
}
